@import url(https://fonts.googleapis.com/css2?family=Red+Hat+Text:wght@700&display=swap);
*,*::after,*::before{margin:0px}*{box-sizing:border-box}body{width:100%;height:100vh;background-color:#1e1f29;background-image:url(/static/media/pattern-hills.815d688f.svg),url(/static/media/bg-stars.166881a1.svg);background-repeat:no-repeat;background-position:bottom;background-position-x:-18em;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;padding:50px;font-size:14px;font-family:'Red Hat Text', sans-serif;color:white}@media screen and (min-width: 675px){body{background-size:135%}}

header h1{font-size:1.2em;text-align:center;margin-top:30%;letter-spacing:0.4em;margin-bottom:20%}@media screen and (min-width: 675px){header h1{margin-top:10%;margin-bottom:15%}}

.value-container{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;padding:2.5px}.value-container .label{padding-top:1em;font-size:0.6em;letter-spacing:0.5em;color:#8486a9}.box{background-color:#191a24;height:5em;width:100%;padding:15px;position:relative;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.box::before{content:'';width:100%;height:50%;background-color:#2a2c41;position:absolute;left:0px;top:0px;border-radius:0.5em}.box::after{content:'';width:100%;height:50%;background-color:#343650;position:absolute;left:0px;top:50%;border-radius:0.5em;box-shadow:0px 3px 0px #191a24}.box p{color:#fb6087;font-size:2em;z-index:1}@media screen and (min-width: 675px){.box{padding:5em;width:90%}.box p{font-size:5em}.value-container{padding:1em}}

main{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;grid-gap:1em;gap:1em;width:100%}

.icon-container{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;grid-gap:2.5em;gap:2.5em;margin-top:70%}@media screen and (min-width: 675px){.icon-container{margin-top:35vh}.icon-container svg{cursor:pointer}}

